.nav {
   z-index: 1;
   position: fixed;
   top: 50px;
   left: 10px;
   display: flex;
   flex-direction: column;
   background-color: white;
   padding: 10px 20px;
   box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
   border-radius: 5px;
}

@media screen and (max-width: 1090px) {
   .nav {
      left: initial;
      top: initial;
      bottom: 0px;
      width: 100%;
      background-color: rgb(205, 255, 248);
   }
}

.nav a {
   text-decoration: none;
   color: #000000;
   text-transform: uppercase;
   font-size: 12px;
   text-align: center;
   font-weight: 600;
}

.nav a:not(:last-child) {
   margin-bottom: 12px;
}
